body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: white;
    color: black;
    padding: 20px;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
  }
  
  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
      monospace;
  }
  
  #root {
    width: 100%;
  }
  
  .calculator-comp {
    align-items: center;
    width: 100%;
    max-width: 400px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    row-gap: 20px;
    padding: 10px;
    background-color: #FFFFFF;
    margin-top: 5%;
    border-radius: 20px;
    box-shadow: -30px 20px 100px 10px rgba(251, 24, 78, 0.50), 30px -20px 100px 10px rgba(47, 24, 251, 0.50);
  }
  
  .calculator-comp>h1 {
    font-size: 2.1rem;
    font-weight: 600;
    color: black;
  }
  
  .calculator-comp>input {
    width: 50%;
    background-color: white;
    color: black;
    border: 1px solid black;
    outline: none;
    padding: 8px;
  }
  
  .btn-grp {
    display: flex;
    justify-content: flex-start;
    column-gap: 25px;
  }
  
  .btn-grp>button {
    height: 35px;
    width: 35px;
    padding: 5px;
    cursor: pointer;
    background-color: black;
    color: white;
  }
  
  .success {
    color: #47F558;
    font-weight: 600;
    font-size: large;
    letter-spacing: 0.05rem;
    text-shadow: -1px -1px 1px black, 1px -1px 1px black, -1px 1px 1px black, 1px 1px 1px black;
  }
  
  .error {
    color: #E33737;
    font-weight: 600;
  }
  